import React, { useState, useEffect, useRef } from "react";
import { NavLink } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";
import MenuOutlinedIcon from "@mui/icons-material/MenuOutlined";
import HomeOutlinedIcon from "@mui/icons-material/HomeOutlined";
import ContentPasteSearchOutlinedIcon from "@mui/icons-material/ContentPasteSearchOutlined";
import SearchIcon from "@mui/icons-material/Search";
import ChecklistRtlOutlinedIcon from "@mui/icons-material/ChecklistRtlOutlined";
import FavoriteBorderOutlinedIcon from "@mui/icons-material/FavoriteBorderOutlined";
import NoteAltOutlinedIcon from "@mui/icons-material/NoteAltOutlined";

import "./Navbar.css";

const Navbar = () => {
  const navigate = useNavigate();
  const [showNav, setShowNav] = useState(false);
  const toggleNav = () => {
    setShowNav(!showNav);
  };

  //to close the menu bar on clicking outside of it
  const menuRef = useRef();
  useEffect(() => {
    document.addEventListener("mousedown", (event) => {
      if (!menuRef.current.contains(event.target)) {
        setShowNav(false);
      }
    });
  });
  //--------------------------------------------------
  const handleLogout = async () => {
    try {
      const response = await axios.get("/api/logout");
      if (response.data.success) {
        toast.success("You were logged out successfully!");
        navigate("/");
      }
    } catch (error) {
      if (
        error &&
        error.response &&
        error.response.data &&
        error.response.data.error
      ) {
        toast.error(error.response.data.error);
      }
    }
  };
  //--------------------------------------------------
  return (
    <nav className="navbar">
      <ToastContainer />
      <div ref={menuRef}>
        <div className="navbar-header">
          {!showNav && window.innerWidth <= 868 && (
            <div className="navbar-toggle" onClick={toggleNav}>
              {/*  */}
              <MenuOutlinedIcon />
            </div>
          )}

          <div className="navbar-logo">
            CI
            <span style={{ color: "rgb(255, 4, 4)" }}>NOVA</span>
          </div>
          <div className="navbar-signin">
            <button
              onClick={() => {
                handleLogout();
              }}
            >
              LOG OUT
            </button>
          </div>
        </div>

        <ul className={showNav ? "navbar-links show" : "navbar-links"}>
          <li className="nav-item">
            <div className="navbar-logomenu">
              CI
              <span style={{ color: "rgb(255, 4, 4)" }}>NOVA</span>
            </div>
          </li>
          <li className="menu">MENU</li>
          <li className="nav-item">
            <NavLink exact to="/Theme" activeClassName="active">
              {showNav && <HomeOutlinedIcon className="nav-item-icon" />}
              <span className="nav-text">HOME</span>
            </NavLink>
          </li>
          <li className="nav-item">
            <NavLink to="/ThemeSearch" activeClassName="active">
              {showNav && <SearchIcon className="nav-item-icon" />}
              <span className="nav-text">SEARCH</span>
            </NavLink>
          </li>
          <li className="nav-item">
            <NavLink to="/GenreSearch" activeClassName="active">
              {showNav && (
                <ContentPasteSearchOutlinedIcon className="nav-item-icon" />
              )}
              <span className="nav-text">GENRESEARCH</span>
            </NavLink>
          </li>
          <li className="nav-item">
            <NavLink to="/Watchlist" activeClassName="active">
              {showNav && (
                <ChecklistRtlOutlinedIcon className="nav-item-icon" />
              )}
              <span className="nav-text"> WATCHLIST</span>
            </NavLink>
          </li>
          <li className="nav-item">
            <NavLink to="/Favourites" activeClassName="active">
              {showNav && (
                <FavoriteBorderOutlinedIcon className="nav-item-icon" />
              )}
              <span className="nav-text"> FAVOURITES</span>
            </NavLink>
          </li>
          <li className="nav-item">
            <NavLink to="/ThemeNote" activeClassName="active">
              {showNav && <NoteAltOutlinedIcon className="nav-item-icon" />}
              <span className="nav-text"> NOTE KEEPER</span>
            </NavLink>
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default Navbar;
