import { useEffect, useState } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";
import Navbar from "./Navbar";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import "./Note.css";

const Notes = () => {
  const [note, setnote] = useState([]);
  const [date, setdate] = useState(new Date());
  const [title, settitle] = useState("");
  const [description, setdescription] = useState("");

  const getdata = async () => {
    const response = await axios.get("/api/getdata");
    if (response.data.success) {
      getdata();
      setnote(response.data.data);
    } else {
      toast.error("Something went wrong.");
    }
  };

  const addnote = async () => {
    const response = await axios.post("/api/addnote", {
      date,
      title,
      description,
    });
    console.log(response);
    if (response.data.success) {
      toast.success("Note saved successfully!");
    } else toast.error("Something Went wrong!");
  };

  const deletenote = async (id) => {
    const response = await axios.delete(`/api/deletenote/${id}`);
    if (response.data.success) {
      getdata();
      toast.success("Data deleted successfully.");
    }
  };

  useEffect(() => {
    getdata();
  }, []);

  return (
    <div className="notebody">
      <div className="note-header">
        <ToastContainer />
        <Navbar />
        <div className="notes">
          <br />
          <h1 style={{ color: "red" }}>Note Keeper</h1>
          <h4 style={{ color: "purple" }}>Just jot down your experiences.</h4>
          <input
            value={date}
            onChange={(e) => setdate(e.target.value)}
            type="date"
          />
          <br />
          <input
            value={title}
            onChange={(e) => settitle(e.target.value)}
            type="text"
            placeholder="Note Title"
          />
          <br />
          <textarea
            value={description}
            onChange={(e) => setdescription(e.target.value)}
            type="text"
            placeholder="Write your Notes here."
          />
          <br />
          <button onClick={() => addnote()}>Add Note</button>
          <br />
        </div>
        <h2 style={{ color: "purple", fontStyle: "italic" }}>
          Your previous notes are here.
        </h2>
        {note.map((v, i) => {
          return (
            <div key={i}>
              <ul className="notedisplay">
                <div className="savednote">
                  <li>{v.date}</li>
                  <li style={{ color: "red" }}>{v.title}</li>
                  <br />
                  <li>{v.description}</li>
                  <br />
                  <button
                    onClick={() => deletenote(v._id)}
                    type="button"
                    style={{
                      backgroundColor: "transparent",
                    }}
                  >
                    <DeleteForeverIcon style={{ fontSize: "40px" }} />
                  </button>
                </div>
              </ul>
            </div>
          );
        })}
      </div>
    </div>
  );
};
export default Notes;
