import React from "react";
import { Routes, Route, BrowserRouter as Router } from "react-router-dom";
import LandingPage from "./component/LandingPage";
import Theme from "./component/Theme";
import Signup from "./component/Signup";
import Signin from "./component/Signin";
import Search from "./component/Search";
import ThemeSearch from "./component/ThemeSearch";
import Notes from "./component/Notes";
import ThemeNote from "./component/ThemeNote";
import GenreSearch from "./component/GenreSearch";
import SingleMovie from "./component/SingleMovie";
import Watchlist from "./component/Watchlist";
import Favourites from "./component/Favourites";
import NotFound from "./component/NotFound";

const App = () => {
  return (
    <>
      <Router>
        <Routes>
          <Route path="/" Component={LandingPage} />
          <Route path="/Theme" Component={Theme} />
          <Route path="/Signup" Component={Signup} />
          <Route path="/Signin" Component={Signin} />
          <Route path="/Search" Component={Search} />
          <Route path="/movie/:id" Component={SingleMovie} />
          <Route path="/GenreSearch" Component={GenreSearch} />
          <Route path="/ThemeSearch" Component={ThemeSearch} />
          <Route path="/Notes" Component={Notes} />
          <Route path="/ThemeNote" Component={ThemeNote} />
          <Route path="/Watchlist" Component={Watchlist} />
          <Route path="/Favourites" Component={Favourites} />
          <Route path="*" Component={NotFound} />
        </Routes>
      </Router>
    </>
  );
};
export default App;
