import React, { useEffect, useState } from "react";
import Navbar from "./Navbar";
import Footer from "./Footer";

const Favourites = () => {
  const [favourite, setFavourite] = useState([]);

  // getting FremoveFromFavourite from local storage
  useEffect(() => {
    const storedFavourite = localStorage.getItem("favourite");
    if (storedFavourite) {
      setFavourite(JSON.parse(storedFavourite));
    }
  }, []);

  const removeFromFavourite = (id) => {
    const updatedFavourite = favourite.filter((movie) => movie.id !== id);
    localStorage.setItem("favourite", JSON.stringify(updatedFavourite));
    setFavourite(updatedFavourite);
  };

  return (
    <>
      <div
        style={{
          backgroundColor: "grey",
          fontWeight: "600",
          height: "100vh",
          textAlign: "center",
        }}
      >
        <Navbar />
        <div style={{ backgroundColor: "grey" }}>
          <h2 style={{ color: "red", fontStyle: "italic" }}>
            My Favourite Movies:
          </h2>
          <ul style={{ color: "white", listStyle: "none" }}>
            {favourite.map((movie) => (
              <li key={movie.id}>
                <img
                  src={`https://image.tmdb.org/t/p/w200${movie.poster_path}`}
                  alt={movie.title}
                  style={{
                    height: "300px",
                    paddingTop: "40px",
                  }}
                />
                <br />
                {movie.title}

                <br />
                <button
                  onClick={() => removeFromFavourite(movie.id)}
                  style={{
                    backgroundColor: "red",
                    color: "white",
                    border: "2px solid red",
                    borderRadius: "4px",
                    padding: "4px",
                    marginTop: "6px",
                  }}
                >
                  Remove
                </button>
              </li>
            ))}
          </ul>
          <br />
          <br />
        </div>
      </div>
    </>
  );
};

export default Favourites;
