import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Box, Typography, CircularProgress } from "@mui/material";
import FavoriteOutlinedIcon from "@mui/icons-material/FavoriteOutlined";
import FavoriteBorderOutlinedIcon from "@mui/icons-material/FavoriteBorderOutlined";
import Navbar from "./Navbar";
import Footer from "./Footer";

const SingleMovie = () => {
  const { id } = useParams();
  const [movie, setMovie] = useState(null);
  const [error, setError] = useState(null);
  const [genres, setGenres] = useState([]);
  const [isAddedToWatchlist, setIsAddedToWatchlist] = useState(false);
  const [favourite, setFavourite] = useState(false);

  useEffect(() => {
    const fetchSingleMovies = async () => {
      try {
        const response = await fetch(
          `https://api.themoviedb.org/3/movie/${id}?api_key=04c35731a5ee918f014970082a0088b1`
        );
        if (!response.ok) {
          throw new Error("failed");
        }
        const data = await response.json();
        setMovie(data);
      } catch (error) {
        setError(error.message);
      }
    };
    const fetchgenre = async () => {
      try {
        const genreResponse = await fetch(
          `https://api.themoviedb.org/3/genre/movie/list?api_key=04c35731a5ee918f014970082a0088b1`
        );
        if (!genreResponse.ok) {
          throw new Error("failed");
        }
        const genreData = await genreResponse.json();
        setGenres(genreData.genres);
      } catch (error) {
        setError(error.message);
      }
    };
    Promise.all([fetchSingleMovies(), fetchgenre()]).catch((error) =>
      setError(error.message)
    );
  }, [id]);

  if (error) {
    return <div>{error}</div>;
  }

  if (!movie) {
    return <div>Loading...</div>;
  }

  const addToWatchlist = () => {
    setIsAddedToWatchlist(true);
    const storedWatchlist = localStorage.getItem("watchlist");
    const updatedWatchlist = storedWatchlist ? JSON.parse(storedWatchlist) : [];
    updatedWatchlist.push(movie);
    localStorage.setItem("watchlist", JSON.stringify(updatedWatchlist));
  };

  const addToFavourite = () => {
    setFavourite(true);
    const storedFavourite = localStorage.getItem("favourite");
    const updatedFavourite = storedFavourite ? JSON.parse(storedFavourite) : [];
    updatedFavourite.push(movie);
    localStorage.setItem("favourite", JSON.stringify(updatedFavourite));
  };
  return (
    <div>
      <Navbar />

      <div
        style={{
          textAlign: "center",
          backgroundColor: "grey",
          fontWeight: "500",
        }}
      >
        <img
          src={`https://image.tmdb.org/t/p/w200${movie.poster_path}`}
          alt={movie.title}
          style={{
            height: "400px",
            paddingTop: "40px",
          }}
        />
        <button
          onClick={addToFavourite}
          style={{
            backgroundColor: "transparent",
            color: "white",
            border: "2px solid transparent",
            cursor: "pointer",
          }}
        >
          {favourite ? (
            <FavoriteOutlinedIcon style={{ color: "red" }} />
          ) : (
            <FavoriteBorderOutlinedIcon />
          )}
        </button>
        <h2 style={{ color: "red" }}>{movie.title}</h2>
        <div style={{ display: "flex", flexWrap: "wrap" }}>
          {genres.map((genre) => (
            <span
              style={{
                backgroundColor: "red",
                color: "white",
                padding: "3px",
                marginRight: "5px",
                marginLeft: "5px",
                marginBottom: "3px",
                borderRadius: "5px",
              }}
            >
              {" "}
              {genre.name}{" "}
            </span>
          ))}
        </div>
        <br />
        {/* --------------------------------------------------------------- */}
        <Box
          style={{
            position: "relative",
            display: "inline-block",
            width: "max-content",
          }}
        >
          <CircularProgress
            variant="determinate"
            value={movie.vote_average * 10}
            color="success"
            size={50}
          />
          <Box
            style={{
              position: "absolute",
              top: 0,
              left: 0,
              bottom: 0,
              right: 0,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Typography
              variant="caption"
              component="div"
              fontWeight="700"
              style={{ marginTop: "-5px", color: "red" }}
            >
              {Math.floor(movie.vote_average * 10) / 10}
            </Typography>
          </Box>
        </Box>
        {/* =============================================================== */}
        <h4 style={{ color: "red" }}>Released On : {movie.release_date}</h4>
        <p>Movie Language: {movie.original_language} </p>
        <div className="overview">
          <p>{movie.overview}</p>
        </div>
        <h5 style={{ color: "red" }}>BACKDROP</h5>
        <img
          src={`https://image.tmdb.org/t/p/w200${movie.backdrop_path}`}
          alt={movie.title}
          style={{ width: "40%" }}
        />
        <br />
        <br />
        <button
          onClick={addToWatchlist}
          style={{
            backgroundColor: "darkgreen",
            color: "white",
            border: "2px solid darkgreen",
            cursor: "pointer",
          }}
        >
          {isAddedToWatchlist ? "Added to Watchlist" : "Add to Watchlist"}
        </button>
        <br />
        <br />
      </div>

      <Footer />
    </div>
  );
};

export default SingleMovie;
