import React, { useState, useEffect } from "react";
import { createContext } from "react";
import ReactSwitch from "react-switch";
import MovieCard from "./MovieCard";
// import "./Theme.css";
import Navbar from "./Navbar";
import Footer from "./Footer";
import DarkModeOutlinedIcon from "@mui/icons-material/DarkModeOutlined";
import LightModeOutlinedIcon from "@mui/icons-material/LightModeOutlined";
export const ThemeContext = createContext(null);

const GenreSearch = () => {
  // ---------------------------------------
  const [theme, setTheme] = useState("dark");
  const toggleTheme = () => {
    setTheme((curr) => (curr === "light" ? "dark" : "light"));
  };
  // ---------------------------------------
  const [genres, setGenres] = useState([]);
  const [selectedGenreId, setSelectedGenreId] = useState(null);
  const [movies, setMovies] = useState([]);
  const apiKey = "04c35731a5ee918f014970082a0088b1";

  useEffect(() => {
    // Fetch list of genres from TMDb API
    fetch(`https://api.themoviedb.org/3/genre/movie/list?api_key=${apiKey}`)
      .then((response) => response.json())
      .then((data) => setGenres(data.genres))
      .catch((error) => console.error("Error fetching genres:", error));
  }, [apiKey]);

  useEffect(() => {
    const fetchMovies = async () => {
      if (selectedGenreId !== null) {
        let allMovies = [];
        let page = 1;
        let totalPages = 1; // Initialize totalPages to a value greater than 0

        // Fetch movies until all pages have been retrieved
        while (page <= totalPages) {
          try {
            const response = await fetch(
              `https://api.themoviedb.org/3/discover/movie?api_key=${apiKey}&with_genres=${selectedGenreId}&page=${page}`
            );
            const data = await response.json();
            allMovies = [...allMovies, ...data.results];

            // Update totalPages based on the total number of pages available
            totalPages = data.total_pages;
          } catch (error) {
            console.error("Error fetching movies:", error);
            break; // Exit the loop if there's an error
          }

          page++; // Move to the next page
        }

        setMovies(allMovies);
      }
    };

    fetchMovies();
  }, [selectedGenreId, apiKey]);

  const handleGenreClick = (genreId) => {
    console.log("click", genreId);
    setSelectedGenreId(genreId);
  };

  return (
    <div style={{ alignItems: "center" }}>
      <ThemeContext.Provider value={{ theme, toggleTheme }}>
        <div className="App" id={theme}>
          <Navbar />
          <br />
          <br />
          <h2 style={{ color: "red" }}>Search your fav genres.</h2>
          <div className="genreSearch">
            <h3>Genres:</h3>
            {genres.map((genre) => (
              <button
                key={genre.id}
                onClick={() => handleGenreClick(genre.id)}
                activeClassName="activegnr"
              >
                {genre.name}
              </button>
            ))}
            <div className="movielist">
              <h3>Filtered Movies:</h3>
              <ul>
                {movies.map((movie) => (
                  <MovieCard key={movie.id} movie={movie} />
                ))}
              </ul>
            </div>
          </div>

          <div className="switch">
            <label>
              {theme === "light" ? (
                <LightModeOutlinedIcon
                  style={{
                    color: "black",
                    paddingRight: "6px",
                    fontSize: "27px",
                  }}
                />
              ) : (
                <DarkModeOutlinedIcon
                  style={{
                    color: "white",
                    paddingRight: "6px",
                    fontSize: "27px",
                  }}
                />
              )}
            </label>

            <ReactSwitch onChange={toggleTheme} checked={theme === "dark"} />
          </div>
        </div>
      </ThemeContext.Provider>
    </div>
  );
};

export default GenreSearch;
