import React from "react";
import { useNavigate } from "react-router-dom";
import "./LandingPage.css";
import feature1 from "./feature-1.png";

const LandingPage = () => {
  const navigate = useNavigate();
  return (
    <>
      <div className="landingpage">
        <nav className="ldnav">
          <div
            className="navbar-logo"
            style={{
              color: "white",
              cursor: "default",
              paddingLeft: "5px",
            }}
          >
            CI
            <span style={{ color: "rgb(255, 4, 4)" }}>NOVA</span>
          </div>
          <button
            className="landingpagebtn"
            onClick={() => {
              navigate("/Signin");
            }}
          >
            SIGN IN
          </button>
        </nav>
        <div className="header-content">
          <h1>Unlimited movies, TV shows and more.</h1>
          <h3>Be updated about the popular trending movies.</h3>
          <p>Signup to explore the featues.</p>
          <button
            className="getbtn"
            onClick={() => {
              navigate("/Signup");
            }}
          >
            GET STARTED{" "}
          </button>
        </div>
      </div>
      <div className="features">
        <div className="row">
          <div className="text-col">
            <h2>Enjoy your movies.</h2>
            <p>
              Be updated about your favourite movies and popular trending
              movies.
            </p>
          </div>
          <div className="img-col">
            <img src={feature1} />
          </div>
        </div>
      </div>
      {/* ----------------------------------------------------------- */}
      <div className="faq">
        <h2>Frequently Asked Questions</h2>
        <ul className="accordion">
          <li>
            <input type="radio" name="accordion" id="first"></input>
            <label for="first">What is CINOVA?</label>
            <div className="ldcontent">
              <p>
                CINOVA is a service that offers you a platform to know details
                about your favourite movies. It also offers you a platform to
                know about the treanding popular Movies. And store your
                experiences too. It gives you a section to create your watchlist
                and many more.
              </p>
            </div>
          </li>
          <li>
            <input type="radio" name="accordion" id="second"></input>
            <label for="second">How much does CINOVA cost?</label>
            <div className="ldcontent">
              <p>CINOVA is a fully free app you don't have to pay any penny.</p>
            </div>
          </li>
        </ul>
      </div>
      {/* ----------------------------Footer------------------------- */}

      <div className="footer" style={{ backgroundColor: "#0c0c0c" }}>
        <div className="title">
          CI
          <span style={{ color: "rgb(255, 4, 4)" }}>NOVA</span>
        </div>
        <a href="#">FAQ</a>
        <br />
        <a href="#">Help Center</a>
        <br />
        <a href="#">Terms of Use</a>
        <br />
        <a href="#">Privacy</a>
        <br />
        <br />
         CINOVA India
        <br />
        <br />
        <small
          style={{ color: "white", fontWeight: "400", marginBottom: "108px" }}
        >
          Ready to start the journey? Join now.
        </small>
        <br />
        <button
          className="getbtn"
          onClick={() => {
            navigate("/Signup");
          }}
        >
          GET STARTED{" "}
        </button>
      </div>
    </>
  );
};
export default LandingPage;
